import React, { Component } from "react";
import { connect } from "react-redux";
import Button from "../../../../dashboard/otherbutton2";
import NavLeft from "./NavLeft";
import NavRight from "./NavRight";
import Aux from "../../../../hoc/_Aux";
import DEMO from "../../../../store/constant";
import * as actionTypes from "../../../../store/actions";

class NavBar extends Component {
  async Logout() {
    localStorage.removeItem("store_id");
    localStorage.removeItem("username");
    localStorage.removeItem("owner_id");
    localStorage.removeItem("isLoggedin");
  }

  render() {
    let headerClass = [
      "navbar",
      "pcoded-header",
      "navbar-expand-lg",
      this.props.headerBackColor,
    ];
    if (this.props.headerFixedLayout) {
      headerClass = [...headerClass, "headerpos-fixed"];
    }

    let toggleClass = ["mobile-menu"];
    if (this.props.collapseMenu) {
      toggleClass = [...toggleClass, "on"];
    }

    return (
      <Aux>
        <header className={headerClass.join(" ")}>
          <div className="m-header">
            <a
              className={toggleClass.join(" ")}
              id="mobile-collapse1"
              href={DEMO.BLANK_LINK}
              onClick={this.props.onToggleNavigation}
            >
              <span />
            </a>
            <a href={DEMO.BLANK_LINK} className="b-brand">
              <div className="b-bg">
                <i className="feather icon-trending-up" />
              </div>
              <span className="b-title">Swip</span>
            </a>
          </div>

          <a className="mobile-menu" id="mobile-header" href={DEMO.BLANK_LINK}>
            <i className="feather icon-more-horizontal" />
          </a>
          <div className="collapse navbar-collapse">
            <NavLeft />
            <NavRight rtlLayout={this.props.rtlLayout} />
          </div>
          <div className="showmobile">
            <div className="showright">
              {" "}
              <a
                onClick={() => {
                  if (window.confirm("Do you wish to Logout?")) {
                    this.Logout();
                  }
                }}
                href={DEMO.BLANK_LINK}
                className="dropdown-item"
              >
                <Button
                  name={"Login"}
                  value={"Logout"}
                  style={{ width: "100% !important" }}
                  placeholder={"Enter your name"}
                />
              </a>
            </div>
            <div className="showleft">
              {" "}
              <a
                href={"http://" + localStorage.getItem("username") + ".swip.ng"}
                target="_blank"
                rel="noopener noreferrer"
                style={{
                  position: "relative",
                  lineHeight: "2em",
                }}
              >
                <Button
                  name={"Login"}
                  value={"Store Front"}
                  style={{ width: "100% !important" }}
                  placeholder={"Enter your name"}
                  handleChange={this.handleFullName}
                />
              </a>
            </div>
          </div>
        </header>
      </Aux>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    rtlLayout: state.rtlLayout,
    headerBackColor: state.headerBackColor,
    headerFixedLayout: state.headerFixedLayout,
    collapseMenu: state.collapseMenu,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    onToggleNavigation: () => dispatch({ type: actionTypes.COLLAPSE_MENU }),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(NavBar);
